import React, { FC } from 'react'
import './Pagination.scss'
import arrow from '../../img/table/arrow.svg'
import arrowRight from '../../img/table/arrowRight.svg'
import { useTypeSelector } from '../../Store'


interface PaginationProps {
    currrentPage: number
    tariffsPerPage: number
    paginate: (pageNumber:number) => void
}

const Pagination:FC<PaginationProps> = ({currrentPage, tariffsPerPage, paginate}) => {

    const pageNumbers = []

    const tariffs = useTypeSelector(state => state.table.tariffs) 

    for (let i = 1; i <= Math.ceil(tariffs.length / tariffsPerPage); i++) {
        pageNumbers.push(i)
        
    }

    function leftPaginate() {
        if (currrentPage < pageNumbers.length) {
            paginate(pageNumbers.length)
        } else {
            paginate(currrentPage - 1)
        }
    }

    function rightPaginate() {
        if (currrentPage >= pageNumbers.length) {
            paginate(1)
        } else {
            paginate(currrentPage + 1)
        }
    }
    

    return (
        <section className="pagination">
            <div className="container">
                <ul className="pagination-block">
                    <li onClick={leftPaginate} className="pagination__item pagination__btn btn"><img className="pagination__btn-left" src={arrow} alt={arrow} /></li>
                    {pageNumbers.map(el => <li style={currrentPage == el ? {color: "#fff", background: "#605DEC"} : {color: "#605DEC"}} onClick={() => paginate(el)} key={el} className="pagination__item btn">{el}</li>)}
                    <li onClick={rightPaginate} className="pagination__item pagination__btn btn"><img className="pagination__btn-right" src={arrow} alt={arrow} /></li>
                </ul>
            </div>
        </section>
    )
}

export default Pagination
