import { ITable } from "../types"

const defaultState:ITable = {
    tariffs: [],
    providers: [],
    address: '',
    loading: false
}

enum TableActionTypes {
    getTariffs = 'getTariffs',
    getProviders = 'getProviders',
    getAddress = 'GetAddress',
    loadingTrue =  'loadingTrue'
}

interface GetTariffsItem {
    type: TableActionTypes.getTariffs
    payload: []
}

interface GetProvidersItem {
    type: TableActionTypes.getProviders
    payload: []
}

interface GetAddress {
    type: TableActionTypes.getAddress
    payload: ''
}

interface LoadingTrue {
    type: TableActionTypes.loadingTrue
    payload: boolean
}

type TableAction = GetTariffsItem | GetProvidersItem | GetAddress | LoadingTrue

export const tableReducer = (state = defaultState, action:TableAction):ITable => {
    switch (action.type) {
        case TableActionTypes.getTariffs:
            return {...state, tariffs: action.payload}
            
        case TableActionTypes.getAddress:
            return {...state, address: action.payload}

        case TableActionTypes.loadingTrue:
            return {...state, loading: action.payload}

        case TableActionTypes.getProviders:
            return {...state, providers: action.payload}

        default:
            return state
    }
}