import React, { FC } from 'react'
import './Comment.scss'
import avatar from '../../img/comment/avatar1.png'

const Comment:FC = () => {

    const comments = [
        {
            id: 1,
            avatar: avatar,
            name: 'Неизвестный гость',
            city: 'Билайн, Москва',
            date: '23.09.2020',
            text: 'Скорость часто падает, и значительно меньше заявленной в тарифе, должно быть 100, но почему то обычно не превышает 50, чаще вообще около 5-20. Соединение очень...'
        },

        {
            id: 2,
            avatar: avatar,
            name: 'Неизвестный гость',
            city: 'Билайн, Москва',
            date: '23.09.2020',
            text: 'Скорость часто падает, и значительно меньше заявленной в тарифе, должно быть 100, но почему то обычно не превышает 50, чаще вообще около 5-20. Соединение очень...'
        },

        {
            id: 3,
            avatar: avatar,
            name: 'Неизвестный гость',
            city: 'Билайн, Москва',
            date: '23.09.2020',
            text: 'Скорость часто падает, и значительно меньше заявленной в тарифе, должно быть 100, но почему то обычно не превышает 50, чаще вообще около 5-20. Соединение очень...'
        }
    ]

    return (
        <section className="section comment">
            <div className="container">
                <h2 className="section__title ">Отзывы о провайдерах</h2>
                <div className="comment-block">
                    <ul className="comment__list">
                        {comments.map(el => {
                            return (
                                <li key={el.id} className="comment__item">
                                    <div className="comment__item-block">
                                        <img src={el.avatar} alt={el.avatar} />
                                    </div>
                                    <div className="comment__item-block">
                                        <h4 className="comment__name">{el.name}</h4>
                                        <h5 className="comment__subtitle">{el.city} | {el.date}</h5>
                                        <p className="comment__text">
                                            {el.text}
                                        </p>
                                    </div>
                                </li>
                            )
                        })}
                    </ul>
                </div>
            </div>
        </section>
    )
}

export default Comment
