import React, { FC } from 'react'
import logo from '../../img/home/Logo.svg'
import icon from '../../img/home/icon.svg'
import { ITariffs } from '../../types'
import { Link } from 'react-router-dom'


const TableString:FC<ITariffs> = ({name, provider, speed, price, url}) => {
    return (
        <tr className="table__string">
            <td className="table__item">
                <div className="table__provider table__wrapper">
                    <img className="table__img" src={logo} alt={logo} />
                    <div className="table__right">
                        <span className="table__title">{name}</span>
                        <p className="table__subtitle">{provider}</p>
                        <span className="table__stock">Акция!</span>
                    </div>
                </div>
            </td>
            <td className="table__item">
                <div className="table__speed">
                    <span className="table__title">{speed}</span>
                    <p className="table__subtitle">Мбит / сек</p>
                </div>
            </td>
            <td className="table__item table__example">
                <div className="table__channel">
                    <span className="table__title">180</span>
                    <p className="table__subtitle">Каналов</p>
                </div>
            </td>
            <td className="table__item ">
                <div className="table__mobile table__wrapper">
                    <div className="table__elem">
                        <span className="table__title">Безлимит</span>
                        <p className="table__subtitle">гб</p>
                    </div>
                    <div className="table__elem">
                        <span className="table__title">1200</span>
                        <p className="table__subtitle">Мин</p>  
                    </div>
                    <div className="table__elem">
                        <span className="table__title">50</span>
                        <p className="table__subtitle">SMS</p>
                    </div>
                </div>
            </td>
            <td className="table__item">
                    <div className="table__wrapper table__option">
                        <img src={icon} alt={icon} />
                        <img src={icon} alt={icon} />
                        <img src={icon} alt={icon} />
                        <img src={icon} alt={icon} />
                    </div>
            </td>
            <td className="table__item">
                <div className="table__wrapper table__price">
                    <div className="table__left">
                        <span className="table__title">{price}</span>
                        <p className="table__subtitle">₽ / Мес</p>
                    </div>
                    <a href={url} className="table__btn btn">Подключить</a>
                </div>
            </td>
        </tr>
    )
}

export default TableString
