import React, { FC, MouseEvent, useState,  } from 'react'
import './SearchForm.scss'
import location from '../../img/searchForm/location.svg'
import axios from 'axios'
import { useDispatch } from 'react-redux'

const SearchForm:FC = () => {

    const dispatch = useDispatch();
    const [street, setStreet] = useState<string>('')
    const [home, setHome] = useState<string>('')

    async function getTariffs(e: MouseEvent<HTMLFormElement>) {
        e.preventDefault()
        if (street !== '' && home !== '') {
            try {
                // const response = await axios.get(`http://212.47.227.248:12000/api/v1/tariffs?address=${street}, ${home}&limit=20&offset=30`);
                const response = await axios.get(`http://212.47.227.248:12000/api/v1/tariffs?address=%D0%BC%D0%BE%D1%81%D0%BA%D0%B2%D0%B0,%20%20%D0%B0%D0%BC%D0%B1%D1%83%D0%BB%D0%B0%D1%82%D0%BE%D1%80%D0%BD%D1%8B%D0%B9%20%D0%BF%D1%80%D0%BE%D0%B5%D0%B7%D0%B4%2017&limit=5&offset=30`);
                const api = response.data;
                console.log(api);
                dispatch({type: "getTariffs", payload: response.data.tariffs})
                dispatch({type: "getProviders", payload: response.data.providers})
                dispatch({type: "GetAddress", payload: response.data.address})
                dispatch({type: "loadingTrue", payload: true})
                

           } catch (error) {
                dispatch({type: "loadingTrue", payload: false})
                return console.log(error)
           }
        }
        
    }

    return (
        <form action="#" className="form search" onSubmit={getTariffs}>
            <label className="search__wrapper search__street">
                <img className="search__img" src={location} alt={location} />
                <input value={street} onChange={(e) => setStreet(e.target.value)} list="street" className="form__input search__input " placeholder="Введите улицу" required/>
                <datalist id="street">
                </datalist>
            </label>
            <label className="search__wrapper search__home">
                <input value={home} onChange={(e) => setHome(e.target.value)} list="home" className="form__input search__input" placeholder="Дом" required/>
                <datalist id="home">
                </datalist>
            </label>
            <label className="search__wrapper search__type">
                <input list="type" className="form__input search__input" placeholder="Тип подключения"/>
                <datalist id="type">
                </datalist>
            </label>
            <input className="search__btn btn" type="submit" value="Показать тарифы" />
        </form>
    )
}

export default SearchForm
