import React, { FC } from 'react'
import './App.scss'
import Header from './Components/Header/Header'
import Footer from './Components/Footer/Footer'
import { HashRouter } from 'react-router-dom'
import Layout from './Components/Layout/Layout'

const App:FC = () => {
  return (
    <HashRouter>
      <Header/>
      <Layout/>
      <Footer/>
    </HashRouter>
  )
}

export default App

