import React, { FC } from 'react'
import { useTypeSelector } from '../../Store'
import './HeroTable.scss'

const HeroTable:FC = () => {

    const address = useTypeSelector(state => state.table.address)

    return (
        <section className="section heroTable">
            <div className="container">
                <div className="heroTable__block">
                    <div className="heroTable__top">
                        <span className="heroTable__subtitle">Подключить интернет / Крюково / Ленино</span>
                        <h1 className="heroTable__title"> 
                            Интернет и ТВ по адресу <br />
                            {address}
                        </h1> 
                    </div>
                </div>
            </div>
        </section>
    )
}

export default HeroTable
