import React, { FC, Fragment } from 'react'
import "./Filter.scss"

const Filter:FC = () => {
    return (
        <Fragment>
            <section className="section filter">
                <div className="container">
                    <div className="filter-block">
                        <button className="filter__button btn">Все</button>
                        <button className="filter__button btn">Домашний интернет</button>
                        <button className="filter__button btn">Интернет + ТВ</button>
                        <button className="filter__button btn">Только телевидение</button>
                        <button className="filter__button btn">Безлимитный интернет</button>
                        <button className="filter__button btn">С мобильной связью</button>
                    </div>
                </div>
            </section>

            <section className="select">
                    <div className="select-block">
                        <select className="select__item btn">
                            <option selected value="Крокодил Гена">Выбрать провайдера</option>
                        </select>
                        <select className="select__item btn">
                            <option selected value="Выбрать провайдера">Сначала популярные</option>
                        </select>
                </div>
            </section>
        </Fragment>
    )
}

export default Filter
